.card-flip12 {
    height: 120px;
    width: 480px;
    background-color: #1d1d1f;
    z-index: 10000;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 24px;
    box-shadow: inset 0 0 0 1px hsl(0deg 0% 51% / 16%);
    padding: 36px 64px 36px 32px;
    border: 1px solid hsla(0, 0%, 51.4%, 0.16);
    transition: all 0.35s ease;
}
.card-flip12:hover {
    cursor: pointer;
    border: 1.5px solid var(--blue-dark);
}
.card-flip-icon {
    z-index: 10000;
    position: absolute;
    right: 5%;
}
.card-flip-front {
    font-weight: 600;
}
#smaller-font-card-flip {
    font-size: 15px;
}

@media screen and (max-width: 768px) {
    .card-flip12 {
        width: 100%;
    }
    #smaller-font-card-flip {
        font-size: 14px;
        line-height: 1.5;
        font-weight: 500;
    }
}
@media screen and (max-width: 375px) {
    .card-flip-content {
        font-size: 13px;
    }
    #smaller-font-card-flip {
        font-size: 11px;
    }
}
@media screen and (min-width: 414px) and (max-width: 414px) {
    .card-flip-content {
        font-size: 14px;
    }
    .card-flip-front {
        font-size: 14px;
    }
    #smaller-font-card-flip {
        font-size: 11.5px;
    }
}
@media only screen and (min-device-width: 414px)
and (max-device-width: 414px)
and (min-device-height: 896px)
and (max-device-height: 896px) {
    #smaller-font-card-flip {
        font-size: 11px;
    }
}

@media only screen and 
(min-device-width: 390px) and 
(max-device-width: 390px) and 
(min-device-height: 844px) and 
(max-device-height: 844px) and 
(-webkit-min-device-pixel-ratio: 3) and 
(min-resolution: 3dppx) and 
(orientation: portrait) {
    #smaller-font-card-flip {
        font-size: 11px;
    }
}

@media screen and (min-width: 390px) and (max-width: 393px) {
    .card-flip-content {
        font-size: 13px;
    }
    .card-flip-front {
        font-size: 14px;
    }
    #smaller-font-card-flip {
        font-size: 10.5px;
    }
}
@media only screen and (min-width: 1024px) and (max-width: 1366px) and (min-resolution: 2dppx)  and (orientation: portrait) {
    .card-flip12 {
        width: 100%;
    }
    #smaller-font-card-flip {
        font-size: 1.18rem;
        line-height: 1.5;
        font-weight: 500;
    }
}
/* media query ipad pro 11 */
@media only screen and (min-width: 834px) and (max-width: 1023px) and (orientation: portrait) and (min-resolution: 2dppx) {
    .card-flip12 {
        width: 100%;
    }
    #smaller-font-card-flip {
        font-size: 1.1rem;
        line-height: 1.5;
        font-weight: 500;
    }
}

/* ****** larger iphones ******* */
/* iphone 13 pro max */
/* iphone 12 pro max */
@media only screen and 
(min-device-width: 428px) and 
(max-device-width: 428px) and 
(min-device-height: 926px) and 
(max-device-height: 926px) and 
(-webkit-min-device-pixel-ratio: 3) and
(min-resolution: 3dppx) and
(orientation: portrait) {
    #smaller-font-card-flip {
        font-size: 11px;
    }
    .card-flip-content {
        font-size: 13px;
    }
    .card-flip-front {
        font-size: 14px;
    }
}

	/* media query for smaller iphones */
/* iphone xs, 13/12 mini, 11 pro, xs, x */
@media only screen and 
(min-device-width: 375px) and 
(max-device-width: 375px) and 
(min-device-height: 812px) and 
(max-device-height: 812px) and
(-webkit-min-device-pixel-ratio: 3) and
(min-resolution: 3dppx) and
(orientation: portrait) {
    #smaller-font-card-flip {
        font-size: 10px;
    }
    .card-flip-content {
        font-size: 12px;
    }
    .card-flip-front {
        font-size: 13px;
    }
}
