
.PS-Wrapper {
    height: 175vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1420px;
    margin-top: 6rem;
}
.ProjectShowcase-Container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    max-width: 1420px;
}
.ProjectShowcase-Row_firstRow,
.ProjectShowcase-Row_secondRow {
        height: 48%;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
}
.ProjectShowcase-Row_firstRow_left,
.ProjectShowcase-Row_firstRow_right,
.ProjectShowcase-Row_secondRow_left,
.ProjectShowcase-Row_secondRow_right,
.ProjectShowcase-Row_thirdRow_left,
.ProjectShowcase-Row_thirdRow_right {
        height: 100%;
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
}
.PS-Container2 {
    height: 50vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.ProjectShowcase-Row_thirdRow {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

/* media queries ipad pro 12 portrait */
@media only screen and (min-width: 1024px) and (max-width: 1366px) and (min-resolution: 2dppx)  and (orientation: portrait) {
    .ProjectShowcase-Container {
        margin-top: -70rem;
    }
    .ProjectShowcase-Row_firstRow {
        height: 10%;
    }
    .ProjectShowcase-Row_secondRow {
        height: 10%;
        margin-top: -10.5rem;
    }
    .PS-Container2 {
        margin-top: -27rem;
    }
}

/* media queries iphone xs max */
@media only screen and (min-device-width: 414px) 
and (max-device-width: 414px)
and (min-device-height: 896px)
and (max-device-height: 896px) {
    .ProjectShowcase-Row_firstRow,
    .ProjectShowcase-Row_secondRow,
    .ProjectShowcase-Row_thirdRow {
        height: 35%;
        display: flex;
        flex-direction: column;
    }
    .ProjectShowcase-Row_secondRow {
        margin-top: 17.5rem;
    }
    .ProjectShowcase-Row_thirdRow {
        margin-top: 19rem;
        height: 70%;
    }
/*     .PS-Wrapper {
        height: 100vh;
    } */
}

/* iphone 13 */
@media only screen and 
(min-device-width: 390px) and 
(max-device-width: 390px) and 
(min-device-height: 844px) and 
(max-device-height: 844px) and 
(-webkit-min-device-pixel-ratio: 3) and 
(min-resolution: 3dppx) and 
(orientation: portrait) {
    .ProjectShowcase-Row_firstRow,
    .ProjectShowcase-Row_secondRow,
    .ProjectShowcase-Row_thirdRow {
        height: 35%;
        display: flex;
        flex-direction: column;
    }
    .ProjectShowcase-Row_secondRow {
        margin-top: 16.5rem;
    }
    .ProjectShowcase-Row_thirdRow {
        margin-top: 17.5rem;
        height: 70%;
    }
}

/* ****** larger iphones ******* */
/* iphone 13 pro max */
/* iphone 12 pro max */
