        height: 70%;
    }
}

/* media queries ipad pro 11 */
@media only screen and (min-width: 834px) and (max-width: 1023px) and (orientation: portrait) and (min-resolution: 2dppx) {
    .ProjectShowcase-Container {
        margin-top: -70rem;
    }
    .ProjectShowcase-Row_firstRow {
        height: 10%;
    }
    .ProjectShowcase-Row_secondRow {
        height: 10%;
        margin-top: -10.5rem;
    }
    .PS-Container2 {
        margin-top: -25rem;
    }
}
	/* media query for smaller iphones */
/* iphone xs, 13/12 mini, 11 pro, xs, x */
@media only screen and 
(min-device-width: 375px) and 
(max-device-width: 375px) and 
(min-device-height: 812px) and 
(max-device-height: 812px) and
(-webkit-min-device-pixel-ratio: 3) and
(min-resolution: 3dppx) and
(orientation: portrait) {
    .ProjectShowcase-Row_firstRow,
    .ProjectShowcase-Row_secondRow,
    .ProjectShowcase-Row_thirdRow {
        min-height: 30rem;
        display: flex;
        flex-direction: column;
    }
    .ProjectShowcase-Row_secondRow {
        margin-top: 0;
    }
    .ProjectShowcase-Row_thirdRow {
        margin-top: 6.8rem;
        height: 70%;
    }
}
