@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Inter", sans-serif;
}

.navbar {
	width: 100%;
	background-color: rgba(29, 29, 31, 0.7);
	backdrop-filter: saturate(180%) blur(20px);
	color: var(--textColor-dark);
	position: fixed;
	left: 0;
	top: 0;
	bottom: auto;
	border-bottom: 1px solid #e2e2e23d;
	-webkit-font-smoothing: antialiased;
	z-index: 999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999;
	transition: 0.4s ease;
}

a,
a:visited {
	text-decoration: none;
	list-style: none;
	color: var(--textColor-dark);
	transition: 0.4s ease;
	/* font-size: 18px; */
}

a:hover {
	cursor: pointer;
	color: var(--blue-dark);
}

.nav-light-active {
	background-color: #ffffffa8;
	transition: 0.65s ease;
	backdrop-filter: saturate(180%) blur(10px);
	color: #1d1d1f;
}

.nav-light-active a,
.nav-light-active a:visited {
	color: #1d1d1fb9;
	text-decoration: none;
	list-style: none;
	transition: 0.4s ease;
}

.nav-light-active a:hover {
	cursor: pointer;
	color: var(--blue-dark);
}

.nav-light-active .contact-arrow-nav {
	color: var(--blue-dark);
}

.nav-light-active .navLogoLink {
	color: #1d1d1f;
}

.nav-light-active .navLogo a:hover {
	color: #1d1d1fb9;
	text-shadow: 0 0 20px var(--blue-dark);
}

.navCta {
	position: relative;
	text-align: center;
	display: flex;
	width: 100%;
	height: 3.5rem;
	justify-content: space-evenly;
}

.navLogo {
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
	font-size: 20px;
	font-weight: 600;
	margin-left: 35px;
}

.navLogo a:hover {
	color: var(--textColor-dark);
	text-shadow: 0 0 20px var(--textColor-dark);
}

.navMenu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 14px;
	font-weight: 500;
	line-height: 16.8px;
	margin-left: -10px;
}

.navMenu a {
	margin-right: 25px;
	padding-right: 5px;
}

.contactLink,
.contactLink:visited {
	display: flex;
	align-items: center;
	color: var(--blue-dark);
	font-size: 16px;
	font-weight: 500;
	margin-right: 60px;
}

.nav-light-active #contactLink-text {
	color: var(--blue-dark);
}

.contactLink:hover,
.contactLink-icon:hover {
	text-decoration: underline;
	cursor: pointer;
}

.contactLink-icon {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-left: 5px;
	padding-top: 2px;
}

/* **************************************************** */
/* Media Queries medium
/* **************************************************** */
@media screen and (max-width: 768px) {

	.navCta {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 3rem;
	}

	.navLogo {
		display: flex;
		justify-content: center;
		align-items: center;
		text-decoration: none;
		font-size: 18px;
		font-weight: 600;
		margin-left: 2rem;
	}

	.navLogo a:hover {
		text-decoration: none;
		cursor: pointer;
	}

	.navMenu {
		display: none;
	}

	.navMenu a {
		margin-right: 0;
		padding-right: 0;
	}

	.contactLink,
	.contactLink:visited {
		display: flex;
		align-items: center;
		color: var(--blue-dark);
		font-size: 16px;
		font-weight: 500;
		margin-right: 2rem;
	}

	.contactLink:hover,
	.contactLink-icon:hover {
		text-decoration: underline;
		cursor: pointer;
	}

	.contactLink-icon {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 5px;
		padding-top: 2px;
	}
}

/* **************************************************** */
/* Media Queries iPhone SE */
/* **************************************************** */
@media screen and (max-width: 375px) {
	.navCta {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
	}

	.navbar {
		width: 100vw;
		position: fixed !important;
		z-index: 1000000;
		top: 0;
		left: 0;
	}

	.navLogo {
		margin-left: 20px;
	}

	.contactLink {
		margin-right: 20px;
	}
}

/* media query ipad pro 11 */
@media only screen and (min-width: 834px) and (max-width: 1023px) and (orientation: portrait) and (min-resolution: 2dppx) {
	.navMenu a {
		/* border: 1px solid red; */
		/* visibility: hidden; */
		font-size: 12px;
	}
	.navMenu {
		margin-left: 1.5rem;
	}
}