.last-body {
    height: 90vh;
    background-color: #1d1d1f;
    position: relative;
    z-index: 300000000;
}
.overflow-container {
	width: 100%;
	max-width: 1450px;
	height: 100%;
	margin-left: auto;
	margin-right: auto;
	padding-left: 28px;
	padding-right: 28px;
}
.last-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.last-text {
    background: linear-gradient(91.36deg, #52f0c8 0%, #49d6b3 13.02%, #63c9d6 25.52%, #A58DE3 37.5%, #56ABEC 49.48%, #737EB7 63.02%, rgb(73, 185, 189) 72.92%, #57acdd 84.38%, #515fdf 97.92%);
    background-size: 200% 200%;
    -webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
    -moz-background-clip: text;
    -webkit-animation: intro-gradient 7.5s infinite ease both;
    -moz-animation: intro-gradient 7.5s infinite ease both;
    animation: intro-gradient 7.5s infinite ease both;
    font-size: 80px;
    line-height: 1.1;
    letter-spacing: -0.045em;
    font-weight: 600;
    max-width: 900px;
    text-align: center;
    position: absolute;
    top: 10%;
    left: 1%;
}
.form-placement {
    height: 70%;
    width: 100%;
    position: absolute;
    top: 28%;
    z-index: 10000;
    max-width: 980px;
}
.contact-container {
    display: flex;
    margin-top: 4rem;
    font-size: 24px;
    color: var(--blue-dark);
}
.contact-container a {
    text-decoration: none;
    list-style: none;
    color: var(--blue-dark);
}
.contact-container a:hover {
    cursor: pointer;
    text-decoration: underline;
}
.contact-container a:visited {
    text-decoration: none;
    list-style: none;
    color: var(--blue-dark);
}
.contact-arrow {
    margin-top: 3px;
    margin-left: 5px;
}
.last-avatar {
    position: absolute;
    top: 45%;
    left: -50%;
    max-width: 450px;
    transform: translateX(-90px) rotate(45deg);
    transition: 0.4s ease;
}
.avatar-active {
    animation: avatar-move 0.5s ease-in-out forwards;
    animation-delay: 0s;
    transition: 0.4s ease;
}
.avatar-active-reverse {
    animation: avatar-reverse 0.5s ease-in-out forwards;
    animation-delay: 0s;
}
.last-header-active {
    animation: animated-q 0.65s ease;
}
footer {
    position: absolute;
    bottom: 0;
    width: 70%;
    left: 14.5%;
    display: flex;
    justify-content: center;
    border-top: 1px solid gray;
    padding: 16px;
    opacity: 0.55;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: -3rem;
}


@keyframes animated-q {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(3deg); }
    50% { transform: rotate(-3deg); }
    75% { transform: rotate(3deg); }
    100% { transform: rotate(0deg); }
}
@keyframes avatar-move {
    from {
        left: -50%;
    }
    to {
        left: 0%;
    }
}
@keyframes avatar-reverse {
    from {
        left: 0%;
    }
    to {
        left: -50%;
    }
}


@-webkit-keyframes intro-gradient {
	0% {
		background-position: 0% 4%;
	}

	50% {
		background-position: 100% 97%;
	}

	100% {
		background-position: 0% 4%;
	}
}

@-moz-keyframes intro-gradient {
	0% {
		background-position: 0% 4%;
	}

	50% {
		background-position: 100% 97%;
	}

	100% {
		background-position: 0% 4%;
	}
}

@keyframes intro-gradient {
	0% {
		background-position: 0% 4%;
	}

	50% {
		background-position: 100% 97%;
	}

	100% {
		background-position: 0% 4%;
	}
}

/* ********************************** */
/* Begin media queries medium screen */
/* ******************************** */
@media screen and (max-width: 768px) {
    .last-text {
        font-size: 54px;
        width: 100%;
        left: -0.4%;
    }
    .last-avatar {
        max-width: 250px;
        transform: translateX(-50px) rotate(45deg);
        top: 73%;
        z-index: 2;
    }
    .last-body {
        max-height: 100vh;
        height: 100vh;
        overflow: hidden;
    }
    footer {
        margin-bottom: 0rem;
        z-index: 1;
    }
}
/* ********************************** */
/*  End media queries medium screen  */
/* ******************************** */

/* ********************************** */
/*   Begin media queries iPhone se   */
/* ******************************** */
@media screen and (max-width: 375px) {
    .last-text {
        font-size: 42px;
    }
    .last-avatar {
        max-width: 170px;
        transform: translateX(-32px) rotate(46deg);
        top: 75%;
        z-index: 2;
    }
    footer {
        transform: scale(0.8);
    }
}
/* ********************************** */
/*    End media queries iPhone se    */
/* ******************************** */

/* ********************************** */
/*   Begin media queries iPhone xr   */
/* ******************************** */
@media screen and (min-width: 412px) and (max-width: 414px){
    .last-text {
        font-size: 42px;
        margin-top: 1rem;
    }
    .last-avatar {
        max-width: 190px;
        transform: translateX(-32px) rotate(46deg);
        top: 75%;
        z-index: 2;
    }
    footer {
        transform: scale(0.8);
    }
}

/* ********************************** */
/*   Begin media queries iPhone 12 pro   */
/* ******************************** */
@media screen and (min-width: 390px) and (max-width: 393px){
    .last-text {
        font-size: 38px;
        margin-top: 1rem;
    }
    .last-avatar {
        max-width: 190px;
